import React, {useEffect, useRef} from 'react'
import './about.css'
import gsap from 'gsap'
import { infLoop } from '../../data'

function AboutHero() {
    useEffect(() => {
        window.scrollTo(0, 0); // Cun ln u trang
    }, []);
    const containerRef = useRef(null); // Tham chiu n container

    useEffect(() => {
        const container = containerRef.current;
        const containerWidth = container.scrollWidth; // Tnh chiu di container
        
        // To hiu ng GSAP
        gsap.to(container, {
        x: `-${containerWidth / 2}px`, // Di chuyn na chiu di container
        duration: 15, // Thi gian hon thnh 1 vng
        ease: "linear",
        repeat: -1, // Lp v tn
        modifiers: {
            x: (x) => parseFloat(x) % (containerWidth / 2) + "px", // Reset v tr
        },
        });
    }, []);
   

    
  return (
    <>
        <div className="About-hero">
            <div className="title">
            <p>Located in the heart of Lan Kwai Fong, we set out to create a space where exceptional cocktails meet unforgettable moments.</p>
            <p>With a focus on creativity, quality ingredients, and a sleek atmosphere, Velvet Shaker has become a go-to destination for those seeking a unique bar experience.</p>
            </div>
            <div className="heading">
            <h1>Longest running cocktail bar in - Hong Kong</h1>
            </div>
        </div>
        <div className="hero-pic">
            <div className="image-container" ref={containerRef}>
                {infLoop.map((val) => (
                    <img src={val.url} alt="" />
                ))}
                {infLoop.map((val) => (
                    <img src={val.url} alt="" />
                ))}
            </div>
        </div>
    </>
  )
}

export default AboutHero